*{
  box-sizing: border-box;
}
.memoList{
  display: flex;
  flex-wrap: wrap;
}
.memoList div{
  position:relative;
  display: flex;
  flex-direction: column;
  width:300px; height: 200px;
  word-wrap: break-word;
  padding:10px;
}
.memoList div img{
  width:100%; height: 100%;
  position:absolute;
  left: 0; top:0;
  z-index: -1;
}
.memoList div button{
  position:absolute;
  right:0; top:0;
}